import{
	RECORD_USERINFO,
	RECORD_USERURL,	
	RECORD_USERNAME,
	RECORD_APPLYCONDITION
}from './mutation-types.js'
import {setStore, getStore} from '../config/mUtils'
export default {
	// 记录用户信息
	[RECORD_USERINFO](state, info) {
		state.userInfo = info;
		state.login = true;
	},
	[RECORD_USERURL](state,url){
		state.userUrl=url
	},
	[RECORD_USERNAME](state,name){
		state.userName=name
	},
	[RECORD_APPLYCONDITION](state,applyCondition){
		state.applyCondition=applyCondition
	}
}


