import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './action'
import getters from './getters'

Vue.use(Vuex)

const state = {
	userInfo: null, //用户信息
	userUrl:null,
	userName:null,
	login:false,//用户是否登录
	applyCondition:null,
}

export default new Vuex.Store({
	state,
	getters,
	actions,
	mutations
})


