var routeList = [];
var firstRouterList = ['investProjectManageList', 'administrativeManageHomepage', 'administrativeManageHomepageNew', 'applyListNewFirst'];
var routeName = {
	'applyList': '审批列表',
	'applyListNew': '审批列表',
	'applyListNewFirst': '审批列表',
	'investProjectManageList': '项目管理',
	'investProjectManageHomepage': '项目管理首页',
	'createApplyHomepage': '新建审批首页',
	'createApplyHomepageNew': '新建审批首页',
	'createApply': '新建审批',
	'createApplyNew': '新建审批',
	'updateApplyNew': '修改审批',
	'viewApply': '查看审批',
	'viewApplyNew': '查看审批',
	'applytempListNew': '审批模版列表',
	'applytempProcessNew': '审批流程配置',
	'administrativeManageHomepage': '行政管理',
	'administrativeManageHomepageNew': '行政管理',
}
//  判断是否需要登录权限 以及是否登录
router.beforeEach((to, from, next) => {
	let local_routelist = JSON.parse(getStore('htcx_routelist'));
	if(from.name == null && local_routelist != undefined) {
		routeList = local_routelist
	}
	if(firstRouterList.indexOf(to.name) != -1) {
		routeList = [];
	}
	var index = -1;
	for(var i = 0; i < routeList.length; i++) {
		if(routeList[i].name == to.name) {
			index = i;
			break;
		}
	}
	if(index !== -1) {
		//如果存在路由列表，则把之后的路由都删掉
		routeList.splice(index + 1, routeList.length - index - 1);
	} else {
		if(from.name == 'login' && to.name == 'applyListNewFirst') {
			this.$router.push({
				name: 'login'
			})
		} else {
			routeList.push({
				"name": to.name,
				"path": to.fullPath,
				"query": to.query,
				"name_str": routeName[to.name]
			});
		}
		//routeList.push({ "name": to.name, "path": to.fullPath, "query": to.query, "name_str": routeName[to.name] });
	}
	to.meta.routeList = routeList;
	setStore('htcx_routelist', routeList);
	next()
})

export default router


