/**

 * md5加密参数
*/
export const addSignature = params => {
	if(!params) return {
		signature: md5('htcx_')
	};
	if(typeof params !== 'object') return;
	// console.log(params);
	let tmp = [];
	Object.keys(params).sort().forEach(function(k) {
		if(k != 'signature') {
			tmp.push(k);
		}
		// console.log()
		if(params[k]==undefined){
			params[k] = '';
		}
	});
	tmp = tmp.join('&');
	// console.log('htcx_' + tmp);
	params.signature = md5('htcx_' + tmp);

	return params
}
export const getsignature = params => {
	if(!params) return {
		signature: md5('htcx_')
	};
	// console.log(params);
	let tmp = params;
	tmp.sort();
	tmp = tmp.join('&');
	let signature = md5('htcx_' + tmp);
	return signature
}
