export const isBroswer = () => { //检测浏览器内核--返回的是两个key，name：浏览器内核的名称---version：浏览器的版本号
	var _broswer = {};
	var sUserAgent = navigator.userAgent;
	var isOpera = sUserAgent.indexOf("Opera") > -1;
	if(isOpera) {
		//首先检测Opera是否进行了伪装
		if(navigator.appName == 'Opera') {
			//如果没有进行伪装，则直接后去版本号
			_broswer.version = parseFloat(navigator.appVersion);
		} else {
			var reOperaVersion = new RegExp("Opera (\\d+.\\d+)");
			//使用正则表达式的test方法测试并将版本号保存在RegExp.$1中
			reOperaVersion.test(sUserAgent);
			_broswer.version = parseFloat(RegExp['$1']);
		}
		_broswer.opera = true;
		_broswer.name = 'opera';
	}
	var isChrome = sUserAgent.indexOf("Chrome") > -1;
	if(isChrome) {
		var reChorme = new RegExp("Chrome/(\\d+\\.\\d+(?:\\.\\d+\\.\\d+))?");
		reChorme.test(sUserAgent);
		_broswer.version = parseFloat(RegExp['$1']);
		_broswer.chrome = true;
		_broswer.name = 'chrome';
	}
	//排除Chrome信息，因为在Chrome的user-agent字符串中会出现Konqueror/Safari的关键字
	var isKHTML = (sUserAgent.indexOf("KHTML") > -1 ||
			sUserAgent.indexOf("Konqueror") > -1 || sUserAgent
			.indexOf("AppleWebKit") > -1) &&
		!isChrome;
	if(isKHTML) { //判断是否基于KHTML，如果时的话在继续判断属于何种KHTML浏览器
		var isSafari = sUserAgent.indexOf("AppleWebKit") > -1;
		var isKonq = sUserAgent.indexOf("Konqueror") > -1;
		if(isSafari) {
			var reAppleWebKit = new RegExp("Version/(\\d+(?:\\.\\d*)?)");
			reAppleWebKit.test(sUserAgent);
			var fAppleWebKitVersion = parseFloat(RegExp["$1"]);
			_broswer.version = parseFloat(RegExp['$1']);
			_broswer.safari = true;
			_broswer.name = 'safari';
		} else if(isKonq) {
			var reKong = new RegExp(
				"Konqueror/(\\d+(?:\\.\\d+(?\\.\\d)?)?)");
			reKong.test(sUserAgent);
			_broswer.version = parseFloat(RegExp['$1']);
			_broswer.konqueror = true;
			_broswer.name = 'konqueror';
		}
	}
	// !isOpera 避免是由Opera伪装成的IE
	var isIE = sUserAgent.indexOf("compatible") > -1 &&
		sUserAgent.indexOf("MSIE") > -1 && !isOpera;
	if(isIE) {
		var reIE = new RegExp("MSIE (\\d+\\.\\d+);");
		reIE.test(sUserAgent);
		_broswer.version = parseFloat(RegExp['$1']);
		_broswer.msie = true;
		_broswer.name = 'msie';
	}
	// 排除Chrome 及 Konqueror/Safari 的伪装
	var isMoz = sUserAgent.indexOf("Gecko") > -1 && !isChrome && !isKHTML;
	if(isMoz) {
		var reMoz = new RegExp("rv:(\\d+\\.\\d+(?:\\.\\d+)?)");
		reMoz.test(sUserAgent);
		_broswer.version = parseFloat(RegExp['$1']);
		_broswer.mozilla = true;
		_broswer.name = 'mozilla';
	}
	return _broswer;
}
//消息弹窗
export const dialogConfirm = (icontype, text) => {
	Vue.prototype.$message({
		message: text,
		type: 'error',
		iconClass: 'successBox-icon-' + icontype,
		customClass: 'successBox',
		center: 'ture',
		duration: 2000
	})
}
//确认弹窗
export const dialogConfirmHasbtn = (text) => {
	return Vue.prototype.$confirm(text, {
		confirmButtonText: '确定',
		cancelButtonText: '取消',
		customClass: 'confirmBox',
		cancelButtonClass: 'confirmBox-cancelbtn',
		confirmButtonClass: 'confirmBox-confirmbtn',
		showClose: false,
		type: 'warning'
	})
}

//金钱格式处理
export const numFormat = (num) => {
	let res = num.toString().replace(/\d+/, function(n) { // 先提取整数部分
		return n.replace(/(\d)(?=(\d{3})+$)/g, function($1) {
			return $1 + ",";
		});
	})
	return res;
}

//验证是否是数字
export const isValidNumber = (str) => {
	let reg = /^[0-9]*$/;
	let result = reg.test(str);
	return result;
}

//验证数字和小数点
export const isValidDecimal = (str) => {
	let reg = /^\d*\.{0,9}\d{0,9}$/;
	let result = reg.test(str);
	return result;
}

//验证手机格式
export const isValidTel = (str) => {
	let reg = /^1(3|4|5|7|8)\d{9}$/;
	let result = reg.test(str);
	return result;
}
//验证身份证格式
export const isValidIdentify = (str) => {
	let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
	let result = reg.test(str);
	return result;
}
//验证金额长度
export const isValidMoneyLength = (str) => {
	if(parseInt(str)>=100000000){
		return true
	}else{
		return false;
	}
}
// 获取选中下拉所选的值 
export const getSelectedItem = (data,prop,selectedId) => {
	let obj = {};
	obj = data.find((item)=>{
		return item[prop] == selectedId;
	});
	return obj
}
