//实缴规模 认缴规模 6位小数点数据处理
export const format_money =(str)=>{
	str = str.toString();
	if(str.includes(".")){
		var reg = /0$/;
		if(reg.test(str)){ // 说明小数后面没有0
			var get_zore_index = str.lastIndexOf("0");
			var opts = str.substr(0,get_zore_index);
			return format_money(opts)
		} else {
			if(str.endsWith(".")){
				return str.split(".")[0]
			} else {
				return str
			}
		}
	} else {
		return str
	}
}


